/* Projects section css */

.container{
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color:black;
  padding: 40px;
  flex-wrap: wrap;
}
.container .box{
  position: relative;
  width: 320px;
  height: 400px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 40px 30px;
  transition: 0.5s;
}
.container .box::before{
  content: "";
  position: absolute;
  top: 0;
  left: 50px;
  width: 50%;
  height: 100%;
  background: white;
  border-radius: 8px;
  transform: skewX(15deg);
  transition: 0.5s;
}
.container .box::after{
  content: "";
  position: absolute;
  top: 0;
  left: 50px;
  width: 50%;
  height: 100%;
  background: white;
  border-radius: 8px;
  transform: skewX(15deg);
  transition: 0.5s;
  filter: blur(30px);
}
.container .box:hover:after,
.container .box:hover:before{
  transform: skewX(0deg);
left: 20px;
width: calc(100%);
}

.container .box:nth-child(1):after,
.container .box:nth-child(1):before{
background: linear-gradient(315deg,#ffbc00,#ff0058);

}
.container .box:nth-child(2):after,
.container .box:nth-child(2):before{
background: linear-gradient(315deg,#03a9f4,#ff0058);

}
.container .box:nth-child(3):after,
.container .box:nth-child(3):before{
background: linear-gradient(315deg,#4dff03,#00d0ff);

}
.container .box span{
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 50;
  pointer-events: none;
}
.container .box span::before{
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 8px;
  background: rgba(255,255,255,0.1);
  backdrop-filter: blur(10px);
  opacity: 0;
  transition: 0.5s;
  pointer-events: none;
  animation: animate 2s ease-in-out infinite;
  box-shadow: 0 5px rgba(0,0,0,0.05);
}
.container .box:hover span::before{
top: -50px;
left: 50px;
width: 100px;
height: 100px;
opacity: 1;
}
.container .box span::after{
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  
  border-radius: 8px;
  background: rgba(255,255,255,0.1);
  backdrop-filter: blur(10px);
  opacity: 0;
  transition: 0.5s;
  pointer-events: none;
  animation: animate 2s ease-in-out infinite;
  box-shadow: 0 5px rgba(0,0,0,0.05);
  animation-delay: -1s;
}
.container .box:hover span::after{
bottom: -50px;
right: 50px;
width: 100px;
height: 100px;
opacity: 1;
}

@keyframes animate {
  
0%,100%{
  transform: translate(Y10px);
}
50%{
  transform: translate(-10px);
}
}

